/**
 * hasOwnProperty
 */
export const hasOwnProperty = Object.prototype.hasOwnProperty;

/**
 * toString
 */
export const toString = Object.prototype.toString;

export function typeOf(value: any) {
	if (null === value) {
		return "null";
	}

	let type = typeof value;

	if ("undefined" === type || "string" === type) {
		return type;
	}

	let typeString = toString.call(value);
	switch (typeString) {
		case "[object Array]":
			return "array";
		case "[object Date]":
			return "date";
		case "[object Boolean]":
			return "boolean";
		case "[object Number]":
			return "number";
		case "[object Function]":
			return "function";
		case "[object RegExp]":
			return "regexp";
		case "[object Object]":
			if (undefined !== value.nodeType) {
				if (3 === value.nodeType) {
					return /\S/.test(value.nodeValue) ? "textnode" : "whitespace";
				} else {
					return "element";
				}
			} else {
				return "object";
			}
		default:
			return "unknow";
	}
}

/**
 * 
 * @param   {Any}      n    
 * @returns {Boolean}
 */
export const isNumber = (n: any): boolean => {
	return typeOf(n) === "number";
};

/**
 * 
 * @param   {Any}      n    
 * @returns {Boolean}
 */
export const isString = (n: any): boolean => {
	return typeOf(n) === "string";
};

/**
 * boolean
 * @param   {Any}      n    
 * @returns {Boolean}
 */
export const isArray = (n: any): boolean => {
	return typeOf(n) === "array";
};

/**
 * keys
 * 1. keys-
 * @param   {string[]} originKeys 
 * @param   {string[]} indexList  indexList
 * @returns {string[]}
 */
export const mergeKeys = (originKeys: string[], indexList: number[] = []): string[] => {
	const arrayLevelCopy = [...indexList];
	const keys = originKeys.reverse().map((key: string) => {
		if (key === "-") {
			const index = arrayLevelCopy.pop();

			return (typeof index === "undefined" ? "" : index).toString();
		}

		return key;
	});

	keys.reverse();

	return keys;
};
