import { JSONSchema6 } from "json-schema";

import { resolve, getDataKeysBySchemaKeys } from "../libs/resolve";

const itemsName = "items";

/**
 * schematype=array
 * items,schema.item
 * @param  {JSONSchema6} schema    schema
 * @param  {String}      schemaPath schemaPath ,example "a#/properties/b/properties/c"
 * @return {JSONSchema6}           schema,example "{title:'21',$id:'a'}"
 */
export default (schemaPath: string, schema: JSONSchema6) => {
    let { items } = schema;

    if (items) {
        const itemSchemaPath = [schemaPath, itemsName].join("/");
        const mergeSchema = resolve(items as JSONSchema6, itemSchemaPath);
        const keys: string[] = getDataKeysBySchemaKeys(itemSchemaPath);

        Object.assign(mergeSchema, {
            keys
        });
    }

    return schema;
};
